import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import Form from "@rjsf/bootstrap-4";
import { TopicService } from '../../../../service/TopicService';


const TopicCrud = () => {

    const navigate = useNavigate();

    const schema = {
        title: "Topic",
        type: "object",
        properties: {
            parentName: {
                "type": "string"
            },
            name: {
                "type": "string"
            },
            title: {
                "type": "string"
            }
        }

    };

    const uiSchema = {
        "ui:order": ["parentName", "name", "title"]
    };

    const [addTopicOpen, setAddTopicOpen] = useState(false);

    const [topics, setTopics] = useState([]);

    const [formData, setFormData] = useState({

    });

    const [loadPage, setLoadPage] = useState(true);


    const loadTopics = async () => {
        const resJson = await TopicService.fetchTopics();
        console.log("resJson = ", resJson);
        setTopics(resJson.data);
    }

    useEffect(() => {
        loadTopics();
    }, [loadPage]);

    const postData = () => {
        console.log('postdata');
        TopicService.addTopic(formData.state.formData)
        setAddTopicOpen(false);
        setLoadPage(true);
        navigate('/topic');
    }

    const renderCreateForm = () => {
        return (
            <>
                <Form schema={schema} uiSchema={uiSchema} onSubmit={postData} ref={(form) => setFormData(form)} />
            </>
        )
    }

    const renderShowTable = () => {
        return (
            <>
                <table class="table">
                    <thead>
                        <tr>
                            <th>id</th>
                            <th>Parent</th>
                            <th>name</th>
                            <th>title</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            topics.map((topic) => (
                                <>
                                    <tr>
                                        <td>{topic.id}</td>
                                        <td>{topic.parentName}</td>
                                        <td>{topic.name}</td>
                                        <td>{topic.title}</td>
                                    </tr>
                                </>
                            ))
                        }

                    </tbody>
                </table>
            </>
        )
    }

    const renderAddtopic = () => {
        return (
            <>
                <button type="button" class="btn btn-primary" onClick={() => setAddTopicOpen(true)}>Add Topic</button>
            </>
        )
    }

    const renderQuestion = () => {
        return (
            <>
                <div class="container-fluid">
                    <div class="row">
                        <div class="col">
                            {renderAddtopic()}
                            {addTopicOpen && renderCreateForm()}
                        </div>
                        <div class="w-100"></div>
                        <div class="col">
                            {!addTopicOpen && renderShowTable()}
                        </div>
                    </div>
                </div>
            </>
        )
    }

    const renderTopicCrud = () => {
        return (
            <>
                <div id="main" class="main">

                    <div class="pagetitle">
                        <h1>Quiz</h1>
                        <nav>
                            <ol class="breadcrumb">
                                <li class="breadcrumb-item"><a href="index.html">Home</a></li>
                                <li class="breadcrumb-item active">Topic</li>
                            </ol>

                            {loadPage && renderQuestion()}
                            <div class="section dashboard">
                                <div class="row">
                                    <div class="col-lg-8">
                                        <div class="row">

                                        </div>
                                    </div>
                                </div>
                            </div>
                        </nav>
                    </div>
                </div>
            </>
        )
    }

    return (
        <>
            {renderTopicCrud()}
        </>
    )


}

export default TopicCrud;