import React, { useRef, useState } from 'react';
//import Form from "@rjsf/bootstrap-4";
import { QuestionService } from '../../../../service/QuestionService';
import { Question } from '../../../models/quiz/Question';
import { Option } from '../../../models/quiz/Option';
import { Answer } from '../../../models/quiz/Answer';
//import Form from 'react-bootstrap/Form';

const QuestionMaker = () => {

    const formInput = useRef();

    const schema = {
        title: "Question",
        type: "object",
        properties: {
            text: {
                "type": "string"
            },
            option: {
                type: "array",
                items: {
                    type: "object",
                    properties: {
                        text: {
                            type: "string"
                        }
                    }
                }
            },
            answerRequestVo: {
                type: "object",
                properties: {
                    text: {
                        type: "string"
                    }
                }
            }
        }

    }

    const formSubmittedHandler = event => {
        event.preventDefault();
        console.log("formSubmittedHandler")
        console.log(formInput.current.question.value);
        var question = new Question();
        question.text = formInput.current.question.value;
        question.topicName = formInput.current.topicName.value;



        var optionVos = new Array();
        optionVos.push(formInput.current.option0.value);
        optionVos.push(formInput.current.option1.value);
        optionVos.push(formInput.current.option2.value);
        optionVos.push(formInput.current.option3.value);

        var rights = new Array();
        rights.push(formInput.current.right0.checked);
        rights.push(formInput.current.right1.checked);
        rights.push(formInput.current.right2.checked);
        rights.push(formInput.current.right3.checked);


        [0, 1, 2, 3].forEach(i => {

            var option = new Option();
            option.right = rights[i];
            option.text = optionVos[i];
            question.orvl.push(option);
        })

        var answer = new Answer();
        answer.text = formInput.current.answerRequestVo.value;
        question.arvl.push(answer);

        console.log("question = ", question);

        QuestionService.addQuestion(question);
    }

    const renderoptionVos = () => {
        return (
            <>
                <label>Options</label>
                {
                    [0, 1, 2, 3].map(i => {
                        return (
                            <div class="input-group mb-3">
                                <div class="input-group-prepend">
                                    <div class="input-group-text">
                                        <input type="checkbox" id={`right${i}`} />
                                    </div>
                                </div>
                                <input type="text" class="form-control" placeholder={`option${i}`} id={`option${i}`} />
                            </div>
                        )
                    })
                }
            </>
        )
    }

    const questionForm = () => {
        return (
            <>
                <div >
                    <form onSubmit={formSubmittedHandler} ref={formInput} className='form-row'>
                        <div className="form-group form-row">
                            <div className='col-sm-2'>
                                <label for="question" className='col-form-label'>Question</label>
                            </div>
                            <div className='col-sm-12'>
                                <textarea class="form-control" id="question" placeholder="Enter Question" />
                            </div>
                        </div>

                        <div className="form-group form-row">
                            <div className='col-sm-2'>
                                <label for="topic" className='col-form-label'>Topic</label>
                            </div>
                            <div className='col-sm-12'>
                                <input type='text' class="form-control" id="topicName" placeholder="topic" />
                            </div>
                        </div>


                        {renderoptionVos()}

                        <div className='form-group form-row'>
                            <label for="answerRequestVo" className='col-form-label'>answer</label>
                            <textarea class="form-control" placeholder="answerRequestVo" id='answerRequestVo' />
                        </div>

                        <button type="submit" class="btn btn-primary">Submit</button>
                    </form>
                </div>

            </>
        )
    }



    return (
        <>
            {questionForm()}

        </>

    );
}

export default QuestionMaker;

