import { AnimaGetRequest } from '../components/models/common/AnimaGetRequest';
import { Question } from './../components/models/quiz/Question';
import http from "./CommonService";

const addRequest = '/question';
const getRequest = '/question/get';
var animaGetRequest = new AnimaGetRequest();
const request = {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(animaGetRequest)
};

export class QuestionService {
    static async fetchQuestions() {
        var response = await http.post(getRequest, animaGetRequest);
        console.log("response = ", response);
        return response.data;
    }

    static async addQuestion(question: Question) {
        const res = await http.post(addRequest, question);
        return res.data;
    }
}