import React from 'react';

const SaveFormData = (props) => {

    const createForm = () => {
        return (
            <>
                <div >
                    <form onSubmit={props.formSubmittedHandler} ref={props.formInput} className='form-row'>
                        <div className="form-group form-row">
                            {

                                props.formInputs.map((formInput) => (
                                    <>
                                        <div className='col-sm-2'>
                                            <label for={formInput.id} className='col-form-label'>{formInput.label}</label>
                                        </div>
                                        <div className='col-sm-12'>
                                            {formInput.type !== 'textarea' && <input type={formInput.type} class="form-control" id={formInput.id} placeholder={formInput.placeholder} />}
                                            {formInput.type === 'textarea' && <textarea class="form-control" id={formInput.id} placeholder={formInput.placeholder} />}
                                        </div>
                                    </>
                                ))
                            }
                        </div>

                        <br></br>
                        <button type="submit" class="btn btn-primary">Submit</button>


                    </form>
                </div>

            </>
        )
    }

    return (
        <>
            {createForm()}
        </>
    )
}
export default SaveFormData;