import { URLConstants } from './URLConstants';
import { Topic } from './../components/layout/main/topic/Topic';
import { AnimaGetRequest } from '../components/models/common/AnimaGetRequest';
import http from "./CommonService";


var animaGetRequest = new AnimaGetRequest();
const request = {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(animaGetRequest)
};


export class TopicService {

    static async fetchTopics() {
        var response = await http.post('/topic/get', animaGetRequest);
        return response.data;
    }

    static async addTopic(topic: Topic) {
        const res = await http.post("/topic/add", topic);
        return res.data;
    }
}