import React, { useState, useEffect } from 'react';
import { QuestionService } from '../../../../service/QuestionService';
import QuestionMaker from './QuestionMaker';


const QuestionCrud = () => {


    const [addQuestion, setAddQuestion] = useState(false);
    const [questions, setQuestions] = useState([]);

    const loadTopics = async () => {
        const resJson = await QuestionService.fetchQuestions();
        console.log("resJson = ", resJson);
        setQuestions(resJson.data);
    }

    useEffect(() => {
        loadTopics();
    }, []);


    const renderShowTable = () => {
        return (
            <>
                <table class="table">
                    <thead>
                        <tr>
                            <th>Id</th>
                            <th>Topic</th>
                            <th>Question</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            questions.map((question) => (
                                <>
                                    <tr>
                                        <td>{question.id}</td>
                                        <td>{question.topicName}</td>
                                        <td>{question.text}</td>
                                    </tr>
                                </>
                            ))
                        }

                    </tbody>
                </table>
            </>
        )
    }

    const renderAddQuestion = () => {
        return (
            <>
                <button type="button" class="btn btn-primary" onClick={() => setAddQuestion(true)}>Add Question</button>
            </>
        )
    }

    return (
        <>
            <div class="container-fluid">
                <div class="row">
                    <div class="col">
                        {renderAddQuestion()}
                        {addQuestion && <QuestionMaker />}
                    </div>
                    <div class="w-100"></div>
                    <div class="col">
                        {!addQuestion && renderShowTable()}
                    </div>
                </div>

            </div>


        </>
    )
}

export default QuestionCrud;