import React, { useState, useEffect } from 'react';

const AddButtonComponent = (props) => {

    const [open, setOpen] = useState(false);


    const renderContent = () => {
        return (
            <>
                <div className='model' id='1' tabIndex="-1" role='dialog' aria-labelledby='exampleModelLabel' aria-hidden='true'>
                    {props.content}
                </div>
            </>
        )
    }

    return (
        <>
            <button type="button" class="btn btn-primary" data-toggle="modal" data-target={`#$1`}
                onClick={() => {
                    setOpen(!open);
                    //props.setClicked(open);
                }}>Add</button>

            {
                open && renderContent()
            }

        </>
    )

}

export default AddButtonComponent;