import React, { useState, useEffect } from 'react';
import { QuestionService } from '../../service/QuestionService';
import { AnimaGetRequest } from '../models/common/AnimaGetRequest';
import { ErrorResponse } from '../models/common/ErrorResponse';
import Quiz2Container from './Quiz2Container';

const StartQuiz = () => {

    const [questions, setQuestions] = useState([]);
    const [error, setError] = useState();
    const [loading, setLoading] = useState(true);
    const [notStarted, setNotStarted] = useState(true);
    const [currentPage, setCurrentPage] = useState(0);

    const loadQuestionBucket = async () => {

        console.log("loadQuestionBucket {");
        setLoading(true);
        var animaGetRequest = new AnimaGetRequest();
        animaGetRequest.pagination.page = currentPage;
        console.log("animaGetRequest", animaGetRequest);
        const requestOptions = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(animaGetRequest)
        };
        try {
            const animaResponse = await QuestionService.fetchQuestions();
            console.log(animaResponse);
            setQuestions(animaResponse.data);
            console.log("questions = ", questions);

        } catch (error) {
            setError(new ErrorResponse(true, error));
        }
        setLoading(false);
        console.log("loadQuestionBucket }");
    };

    useEffect(() => {
        loadQuestionBucket();
    }, [currentPage]);

    const startTest = () => {
        console.log("startTest {");
        setCurrentPage(0);
        setNotStarted(false);
        console.log("startTest }");
    }

    const renderStartTest = () => {
        console.log("renderStartTest")
        return (
            <>
                <div>
                    <div id="next">
                        <button class="btn btn-dark btn-lg btn-block" type='button' onClick={() => startTest()}>Start Test</button>
                    </div>
                </div>
            </>

        )
    }

    const renderStartQuiz = () => {
        return (
            <>
                <div id="main" class="main">

                    <div class="pagetitle">
                        <h1>Start Quiz</h1>
                        <nav>
                            <ol class="breadcrumb">
                                <li class="breadcrumb-item"><a href="index.html">Home</a></li>
                                <li class="breadcrumb-item active">Start Quiz</li>
                            </ol>
                            <div>
                                {notStarted && renderStartTest()}
                                {!notStarted && !loading && <Quiz2Container questions={questions} currentPage={currentPage} setCurrentPage={setCurrentPage} />}
                                {!notStarted && loading && <p>loading...</p>}
                                {/* {!notStarted && error.status && <p>Error happen! {error.msg}</p>} */}
                            </div>

                            <div class="section dashboard">
                                <div class="row">
                                    <div class="col-lg-8">
                                        <div class="row">

                                        </div>
                                    </div>
                                </div>
                            </div>
                        </nav>
                    </div>
                </div>
            </>
        )
    }

    return (
        <>
            {renderStartQuiz()}
        </>
    )
}

export default StartQuiz;

